import React, { useState, useEffect } from "react";

const MovieDetail = ({ match }) => {
    useEffect (() => {
        fetchItem();
        console.log(match)
    }, []);

    const [item, setItem] = useState({});

    const fetchItem = async () => {
        const fetchItem = await fetch(`https://api.themoviedb.org/3/movie/${match.params.id}?api_key=090028bc6de357f7dbfd5aeebae43718`);
        const item = await fetchItem.json();
        setItem(item);

        console.log(item);
    }

    return (
        <>
            <h1>{item.title}</h1>
        </>
    )
}

export default MovieDetail
